<template lang="html">
    <div id="book-category">
        <navigation-bar @tap="" :title="type[typeId-1].name">
          <navigation-bar-item @tap="back" slot="left" text="" icon="back"/>
        </navigation-bar>
        <div class="book-category-intro">
            {{ type[typeId-1].intro }}
        </div>
        <div class="book-category-list">
            <ul>
                <li v-for="book in mockData" :key="book.rank">
                    <div class="book-img">
                        <img :src=" book.img " :alt=" book.name ">
                    </div>
                    <div class="book-info">
                        <p>{{ book.name }}</p>
                        <div class="operate">
                            <label class="addFavor">
                                <input type="checkbox" v-model="favorList" :value="book.id">
                                <icon class="icon" v-if="isFavor(book.id)" :name="'favor'"></icon>
                                <icon class="icon" v-else :name="'unfavor'"></icon>
                            </label>
                            <label class="addCart">
                                <input type="checkbox" v-model="buyList" :value="book.id">
                                <icon :name="'addCart'"></icon>
                            </label>


                        </div>
                    </div>
                    <div class="book-price">
                         {{ book.price }}
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import { NavigationBar, NavigationBarItem } from '@/components/NavigationBar'
import Icon from '@/components/Icon'
export default {
  name: 'bookCategoryList',
  components: {
    NavigationBar,
    NavigationBarItem,
    Icon
  },
  computed: {
    typeId () {
      // $router.params.typeId
      if (this.$route.params.typeId) {
        return this.$route.params.typeId
      } else {
        return 9
      }
    }
  },
  watch: {
  },
  data () {
    return {
      type: [
        {name: '', intro: ''},
        {name: '', intro: ''},
        {name: '', intro: ''},
        {name: '', intro: ''},
        {name: '', intro: ''},
        {name: '', intro: ''},
        {name: '', intro: ',,'},
        {name: '', intro: ''},
        {name: '', intro: ''}
      ],
      mockData: [
        {id: 1, name: '', price: 23.40, rank: 2, img: 'http://img13.360buyimg.com/n3/jfs/t1393/113/77737149/217635/9064dd42/555408dbN8679b564.jpg'},
        {id: 2, name: '', price: 29.90, rank: 1, img: 'http://img13.360buyimg.com/n3/jfs/t526/8/239863987/140707/38421a9e/546d9a25N07687a60.jpg'},
        {id: 3, name: '', price: 17.80, rank: 4, img: 'http://img13.360buyimg.com/n3/jfs/t655/238/1195078491/109034/b41afb59/54bdf6e0Nf74bdaaf.jpg'},
        {id: 4, name: '', price: 22.60, rank: 3, img: 'http://img13.360buyimg.com/n3/g5/M02/14/11/rBEIC1ADeo4IAAAAAAGBNTPspiAAAEAzgGbRD8AAYFN108.jpg'}
      ],
      favorList: [],
      buyList: []
    }
  },
  methods: {
    isFavor (id) {
      return this.favorList.some((x) => {
        return x === id
      })
    },
    back () {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="stylus" scoped>
#book-category {
    background: #efeff4;
}
.book-category-intro {
    text-align: center;
    width: 80%;
    margin: 12px auto;
    font-size: 12px;
    color: #888888;
    font-family: 'SimHei', 'Weibei SC', sans-serif;
}
.book-category-list {
    font-size: 15px;
    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        li {
            padding: 10px 10px;
            border-bottom: 1px solid #d3d3d3;
            display: flex;
            justify-content: space-around;
            align-items: flex-start;
            .book-img {
                img {
                    width: 55%;
                }
            }
            .book-info{
                font-size: 12px;
                flex-grow: 2;
                .operate {
                    display: flex;
                    justify-content: flex-start
                    .addFavor {
                        margin-right: 5px
                        width: 7%
                        input {
                            display: none;
                        }
                    }
                    .addCart {
                        width: 7%
                        input {
                            display: none;
                        }
                    }
                }
            }
            .book-price {
                font-size: 12px;
            }
        }
    }

}
</style>



