      <p class="date"></p>
    </div>
  </div>
</template>

<script>
import {NavigationBar, NavigationBarItem} from '@/components/NavigationBar'
import {BottomBar, BottomBarItem} from '@/components/BottomBar'
import {ActionSheet, ActionSheetItem} from '@/components/ActionSheet'
import ButtonItem from '@/components/ButtonItem'
import Page from './Page'
import Paging from './lib/page.js'
import bounce from './lib/bounce.js'
export default {
  name: 'read',
  components: {
    NavigationBar,
    NavigationBarItem,
    BottomBar,
    BottomBarItem,
    Page,
    ActionSheet,
    ActionSheetItem,
    ButtonItem
  },
  data () {
    return {
      show: false,
      pages: [],
      page: 0,
      finish: false,
      pandistance: 0,
      paning: false,
      showmore: false
    }
  },
  watch: {
    show: function (val) {
      this.refreshStatusBar()
    }
  },
  methods: {
    back () {
      this.$router.back()
    },
    more () {
      console.log('moretap')
      this.showmore = true
    },
    tap (e) {
      var width = window.innerWidth
      // var height = window.innerHeight
      if (this.show) {
        this.show = false
      } else {
        if (e.center.x < width / 3) {
          this.prev()
        } else if (e.center.x > width * 2 / 3) {
          this.next()
        } else {
          this.show = true
        }
      }
    },
    refreshStatusBar () {
      // console.log(this.show)
      if (this.$statusBar && this.$platform === 'ios') {
        if (!this.show) {
          this.$statusBar.hide()
        } else {
          this.$statusBar.show()
        }
      }
    },
    prev () {
      // this.tag = true
      console.log('to prev page')
      if (this.pandistance < 0) {
        this.page++
      }
      if (this.pandistance > 0) {
        this.page--
      }
      this.paning = true
      this.pandistance = 0
      setTimeout(this.swiperight, 50)
      // if (this.page > 0) {
      //   this.page--
      // }
      // console.log(this.pages[this.page])
    },
    next () {
      // this.tag = false
      console.log('to next page')
      if (this.pandistance < 0) {
        this.page++
      }
      if (this.pandistance > 0) {
        this.page--
      }
      this.paning = true
      this.pandistance = 0
      setTimeout(this.swipeleft, 50)
      // if (this.page + 1 < this.pages.length) {
      //   this.page++
      // }
      // console.log(this.pages[this.page])
    },
    panstart (e) {
      if (!this.taging) {
        this.show = false
        this.paning = true
        // this.panStart =
        if (this.pandistance < 0) {
          this.page++
        }
        if (this.pandistance > 0) {
          this.page--
        }
        this.pandistance = 0
        this.fixdistance = e.deltaX
      }
    },
    panHorizontal (e) {
      if (!this.taging) {
        if ((this.page <= 0 && e.deltaX > 0) || (this.page >= this.pages.length - 1 && e.deltaX < 0)) {
          this.pandistance = bounce(e.deltaX - this.fixdistance)
        } else {
          this.pandistance = e.deltaX - this.fixdistance
        }
      }
    },
    panend (e) {
      if (!this.taging) {
        var width = window.innerWidth
        // console.log(e.deltaX - this.fixdistance - this.pandistance)
        // console.log(new Date().getTime() - this.pantime)
        if (this.pandistance < -width * 0.5 && this.page + 1 < this.pages.length) {
          this.pandistance = -width
        } else if (this.pandistance > width * 0.5 && this.page > 0) {
          this.pandistance = width
        } else {
          this.pandistance = 0
        }
        this.paning = false
      }
    },
    swiperight () {
      // console.log('swiperight')
      this.paning = false
      // this.paning = true
      // if (this.pandistance < 0) {
      //   this.page++
      // }
      // if (this.pandistance > 0) {
      //   this.page--
      // }
      // this.paning = false
      if (this.page > 0) {
        var width = window.innerWidth
        this.pandistance = width
      }
    },
    swipeleft () {
      // console.log('swipeleft')
      // if (this.pandistance < 0) {
      //   this.page++
      // }
      // if (this.pandistance > 0) {
      //   this.page--
      // }
      // this.paning = false
      this.paning = false
      if (this.page + 1 < this.pages.length) {
        var width = window.innerWidth
        this.pandistance = -width
      }
    },
    tagstart () {
      this.show = false
      this.taging = true
    },
    tagend () {
      this.taging = false
    },
    getData: function () {
      var dataElement = this.$el.getElementsByClassName('text')[0].getElementsByTagName('p')
      var data = []
      Array.prototype.forEach.call(dataElement, function (value) {
        data.push(value.textContent)
      })
      return data
    },
    paging: function () {
      // var buffer = this.$el.getElementByClassName()
      var buffer = this.$el.getElementsByClassName('buffer')[0]
      var height = this.$el.getElementsByClassName('pageContent')[0].offsetHeight
      var data = this.getData()
      var paging = new Paging()
      console.log(data.length)
      console.log(this.$el.getElementsByClassName('text')[0].textContent.length)
      this.$el.getElementsByClassName('text')[0]
      paging.on('start', () => {
        this.startTime = new Date().getTime()
      })
      paging.on('finish', () => {
        this.finish = true
        console.log(new Date().getTime() - this.startTime)
      })
      paging.on('page', (page) => {
        this.pages.push(page)
        if (this.pages.length > this.page + 2) {
          this.finish = true
        }
      })
      buffer.innerHTML = ''
      paging.start(buffer, height, data, '')
    }
  },
  activated () {
    this.show = false
    this.refreshStatusBar()
  },
  mounted () {
    this.paging()
  }
}
</script>

<style lang="stylus" scoped>
.read {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  user-select: none;
  background: #e2e2e2;
  .navigation{
    position: absolute;
    transform: translateY(-100%);
    transition: transform .3s;
    &[show]{
      transform: translateY(0);
    }
  }
  .content{
    flex: 1;
    display: flex;
    .prev{
      left: -100%;
      right: 100%;
    }
    .next{
      left: 100%;
      right: -100%;
    }
  }
  .trans{   
    transition: transform .4s cubic-bezier(.3,.5,.29,.99);
  }
  .bottomBar{
    position: absolute;
    bottom: 0;
    transform: translateY(100%);
    transition: transform .3s;
    &[show]{
      transform: translateY(0)
    }
  }
  .pageContentWrapper{
    text-indent: 2em;
    text-align: justify;
    line-height: 1.8em;
    font-size: 16px;
    font-family: serif;
  }
  .noindent>:first-child{
    text-indent: 0em;
  }
  .buttonItem{
    height: 53px;
  }
  // .pageContentWrapper p {
  //   -webkit-margin-before: 0.5em;
  //   -webkit-margin-after: 0.5em;
  // }
  // .pageContent p:first-child{
  //   -webkit-margin-before: 0em;
  // }
  // ::-webkit-scrollbar{width:0px;}
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-active {
  opacity: 0
}
</style>



