<template>
  <div id="detail">
    <navigation-bar @tap="scrollTop" title="" ref="scroller" >
      <navigation-bar-item slot="left" icon="back" text="" @tap="back"/>
    </navigation-bar>
     <scroller style="flex-grow:1" ref="scroller" can-pull-refresh @pullRefresh="pullRefresh" @loadMore="loadMore" can-load-more>
        <div class="top">
          <Cover :info='info' />
        </div>
        <div class="space"></div>
        <div class="centerTop">
          <info :data="info.data" :update="update"/>
          <div class="space"></div>
          <relate :initialInfo='recommendInfo'/>
        </div>
        <div class="space"></div>
        <div class="bottom">
          <comment :comment='comment'/>
        </div>
    </scroller>
    <shop v-model="shopModalShow" @paySuccess="showMessage"></shop>
    <message v-model="messageShow" :message-text="messageText"></message>
    <bottom-bar>
      <bottom-bar-item  v-if="!isIn" text="+" icon="add" @tap="add"></bottom-bar-item>
      <bottom-bar-item  v-else text="-" icon="remove" @tap="remove"></bottom-bar-item>
      <bottom-bar-item  text="" icon="read" class="center" ></bottom-bar-item>
      <bottom-bar-item  text="" icon="shop" @tap="shop"></bottom-bar-item>
    </bottom-bar>
  </div>
</template>

<script>
  import Scroller from '@/components/Scroller'
  import { BottomBar, BottomBarItem } from '@/components/BottomBar'
  import { NavigationBar, NavigationBarItem } from '@/components/NavigationBar'
  import Cover from './components/Cover'
  import Info from './components/Info'
  import Relate from './components/relate'
  import Comment from './components/comment'
  import Shop from './components/shop'
  import Message from '@/components/Message'
  export default {
    name: 'detail',
    components: {
      BottomBar,
      BottomBarItem,
      NavigationBar,
      NavigationBarItem,
      Scroller,
      Cover,
      Info,
      Relate,
      Comment,
      Shop,
      Message
    },
    data () {
      return {
        info: {
        },
        update: {
        },
        recommendInfo: [
        ],
        comment: [
        ],
        shopModalShow: false,
        messageShow: false,
        messageText: ''
      }
    },
    mounted () {
      this.info = {
        title: '',
        id: '8877',
        author: '',
        data: '',
        cover: 'https://ss0.baidu.com/6ONWsjip0QIZ8tyhnq/it/u=2619242940,2733301503&fm=58',
        price: 600,
        score: 10}
      this.update = {
        time: '2017-03-18 08:08',
        chapter: 'xxxx  xxxx'
      }
      for (let i = 1; i <= 3; i++) {
        this.recommendInfo.push({cover: 'https://ss0.baidu.com/6ONWsjip0QIZ8tyhnq/it/u=2619242940,2733301503&fm=58',
          title: ''})
      }
      for (let i = 1; i <= 10; i++) {
        this.comment.push({author: 'sx', time: '2017-03-18 08:08', data: '', resNum: 122})
      }
    },
    computed: {
      isIn () {
        var books = this.$store.state.books
        for (let i = books.length - 1; i >= 0; i--) {
          if (this.info.id === books[i].id) {
            return true
          }
        }
        return false
      }
    },
    methods: {
      back () {
        this.$router.go(-1)
      },
      scrollTop: function () {
        this.$refs.scroller.scrollTop()
      },
      pullRefresh (over) {
        console.log('pullRefresh')
        setTimeout(over, 3000)
      },
      loadMore (over) {
        console.log('loadMore')
        setTimeout(() => {
          var noMore = false
          var i = this.comment.length
          var end = i + 10
          console.log(this.comment.length)
          for (; i < end; i++) {
            this.comment.push({author: 'sx', time: '2017-03-18 08:08', data: '', resNum: 122})
          }
          if (i > 100) {
            noMore = true
          }
          over(noMore)
        }, 2000)
      },
      test () {
        alert('')
      },
      add () {
        var book = {'id': this.info.id, 'title': this.info.title, 'cover': this.info.cover, 'isEdit': false}
        this.$store.commit('add', book)
      },
      remove () {
        this.$store.commit('remove', this.info.id)
        console.log(this.isIn)
      },
      shop () {
        this.shopModalShow = true
      },
      showMessage (text) {
        this.messageText = text
        this.messageShow = true
      }
    }
  }
</script>
<style lang="css" scoped>
  #detail{
    width: 100%;
    background: #efeff4;
    display: flex;
    flex-direction: column;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: absolute;
    overflow: hidden;
  }
  .top{
    display: flex;
    flex-direction: column ;
    font-size: 12px;
  }
  .space{
    height: 5px;
    background: white;
  }
  .centerTop{
    display: flex;
    flex-direction: column;
    font-size: 12px;
    padding-top: 10px;
  }
  .bottom{
    font-size: 12px;
  }
  .hr{
    background: #d3d3d3;
    height: 1px;
    margin-bottom: 10px;
  }
  .center{
    flex-grow: 1.5;
    background: #6bc4f5;
  }
</style>



